.social-icons {
    background: #FFF;
}

.social-icons a {
    display: inline-block;
    text-align: center;
    transition: all 0.3s ease;
    color: white;
    font-size: 12px;
    margin: .5rem .1rem;
    padding: 5px 10px;
    border-radius: 50%;
}

.social-icons a:hover {
    opacity: .8;
}

.facebook {
    background: #3B5998;
    color: white;
}

.linkedin {
    background: #007bb5;
    color: white;
}

@media (min-width: 1200px) {
    .social-icons {
        position: fixed;
        top: 50%;
        right: 0;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
        z-index: 200;
    }
    .social-icons a {
        display: block;
        margin: 0;
        padding: 16px;
        font-size: 20px;
        border-radius: 0;
    }
}
.nav-bar {
    background: #FFF;
}

.nav-bar nav {
    min-height: 100px;
}

nav.bg-light {
    background: #FFF !important;
}

nav .nav-link {
    text-transform: uppercase;
    margin: 0 20px;
    font-size: 15px;
}

.navbar-light .navbar-nav .nav-link.active{
    color: #0b6a9e;
    font-weight: 600;
}

a.logo-link:hover {
    text-decoration: none;
}

.logo {
    text-align: right;
    width: 280px;
    margin-top: 20px;
    margin-bottom: 21px;
}

.logo .lead {
    color: #0b6a9e;
    font-size: 40px;
    line-height: 40px;
}

.logo .secondary {
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 5pt;
    color: #99a2a7;
}

nav .secondary-menu.collapsed {
    display: none;
}

nav .submenu {
    width: 250px;
}

@media screen and (max-width: 1200px) {
    nav .navbar-collapse {
        width: 100%;
        text-align: left;
    }
    nav .secondary-menu {
        text-align: center;
        margin: 5px 0;
    }
    nav .dropdown-menu {
        border: 0;
        padding: 0;
        margin: 0 0 0 20px;
    }
    nav .dropdown-menu a.nav-link {
        padding: 5px 0;
        font-size: 0.9em;
    }
    nav .dropdown-item {
        padding: 0 15px;
    }
    nav .secondary-menu.full-width {
        display: none;
    }
    nav .secondary-menu.collapsed {
        display: block;
    }
}
@media screen and (max-width: 400px) {
    .logo {
        text-align: center;
    }
    nav {
        flex-direction: column;
    }
    nav .navbar-toggler {
        margin: 10px;
    }
}
.secondary-menu button {
    text-transform: uppercase;
    color: #99a2a7;
}

.secondary-menu button:hover, .secondary-menu button.active {
    text-decoration: none;
    color: #000;
}

nav .dropdown-menu {
    top: 66px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    min-width: 14rem;
}


#home {
  height: calc(100% - 100px);
  width: 100%;
  position: absolute;
  z-index: 2;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.page-photo {
    height: 300px;
    background: #CCC;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}


.more-button {
    position: absolute;
    bottom: 0;
    right: 0;
}

.product-description {
    position: relative;
}

.breadcrumb {
    background: rgba(255, 255, 255, .5);
}
body {
  font-family: 'Open Sans', sans-serif;
  background: #EEE;
  padding-top: 100px;
}

